import { Component, OnInit, ViewChild } from '@angular/core';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { UsuariosLoginService } from 'src/app/providers/Api/UsuariosLogin.service';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import * as moment from 'moment';
import { Router } from '@angular/router';
import { IonInfiniteScroll } from '@ionic/angular';
@Component({
  selector: 'app-notificaciones',
  templateUrl: './notificaciones.page.html',
  styleUrls: ['./notificaciones.page.scss'],
})
export class NotificacionesPage implements OnInit {

  notificaciones: any = [''];
  desde = 0;
  carga = false;

  @ViewChild('inifinito', {static: false}) infinite: IonInfiniteScroll;

  constructor(
    private almacenamiento: InicioService,
    private usuarioApi: UsuariosLoginService,
    private toastCtrl: ToastServices,
    private router: Router
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.inicio();

  }

  async inicio() {
      const login = await this.almacenamiento.obtenerToken();

      if (login) {
        this.usuarioApi.traerMisNotificaciones(login.token, this.desde).subscribe((data: any) => {
          if (data.exe) {
              
              data.response.forEach((item) => {
                if (item.usuarioCreador) {
                  item.cd = true;
                  if (item.usuarioCreador.imagen.length > 0) {
                    item.imagen = item.usuarioCreador.imagen[0].path;
                  }
                } else {
                    item.cd = false;
                }
                
              })
              this.notificaciones = data.response;
              this.carga = true;
            } else {
              this.toastCtrl.alertApplication('No se pudo traer las notificaciones, intentelo más tarde', false);
            }
        }, (error) => {
          console.log(error)
            this.toastCtrl.alertApplication('No se pudo traer las notificaciones, intentelo más tarde', false);
        })
      }
  }

  refrescando( event ) {
    this.desde = 0;
    this.inicio().then(() => {
      event.detail.complete();
    })
  }

  async loadData( event ) {
    this.desde = this.desde + 10;
    console.log(this.desde)
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

      this.usuarioApi.traerMisNotificaciones(info.token, this.desde).subscribe((data: any) => {
        console.log(data);
        if (data.exe) {
           
            if ( data.response.length > 0  ) {
               
            data.response.forEach((item) => {
              if (item.usuarioCreador) {
                item.cd = true;
                if (item.usuarioCreador.imagen.length > 0) {
                  item.imagen = item.usuarioCreador.imagen[0].path;
                }
              } else {
                  item.cd = false;
              }
              this.notificaciones.push(item)
            })
            event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toastCtrl.alertApplication('No se pudo traer las notificaciones, intentelo más tarde', false);
          }
      }, (error) => {
        console.log(error)
          this.toastCtrl.alertApplication('No se pudo traer las notificaciones, intentelo más tarde', false);
      })

    }
  } 

  async navegar( noti: any) {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.usuarioApi.ActualizarNotificaciones(info.token, noti._id).subscribe((data: any) => {
          if (data.exe) {
            if (noti.accion.tipo === 'Seguimiento') {
              this.router.navigate(['/central/menu/solicitudes']);
            } else if (noti.accion.tipo === 'SeguimientoAceptado') {
              this.router.navigate(['/central/buscar/perfil-visita/', noti.usuarioCreador.nombreusuario]);
            } else if (noti.accion.tipo === 'Productos') {
              this.router.navigate(['/central/menu/ivt-miinventario/', noti.accion.data.id, 'productos', noti.accion.data.id]);
            } else if (noti.accion.tipo === 'SalidaIVT') {
              this.router.navigate(['/central/menu/ivt-miinventario/', noti.accion.data.id, 'salidas', noti.accion.data.id, noti.accion.data.creador]);
            } else if (noti.accion.tipo === 'EntradaIVT') {
              this.router.navigate(['/central/menu/ivt-miinventario/', noti.accion.data.id, 'entradas', noti.accion.data.id]);
            } else if (noti.accion.tipo === 'Publicacion') {
              this.router.navigate(['/igtmpublic/', noti.accion.data.id]);
            }
          }
        })
      }
  }

}
